                      <h4 class="pubtitle">A Long Short-Term Memory (LSTM) Model for Business Sentiment Analysis Based on Recurrent Neural Network</h4>
                      <div class="pubauthor"> Md. Jahidul Islam Razin, <strong>Md. Abdul Karim</strong>, M. F. Mridha, S. M. Rafiuddin Rifat and Tahira Alam</div>
                      <div class="pubcite"><span class="label label-warning">Conference Papers
                      </span>Sustainable Communication Networks and Application, 1-15 Springer, Singapore.</div>

