7-4_1","title":"External link","target":"_blank"}},[_c('i',{staticClass:"icon-external-link"})]),_c('a',{staticClass:"tooltips",attrs:{"href":"https://doi.org/10.1007/978-981-15-8677-4_1","title":"Download","target":"_blank"}},[_c('i',{staticClass:"icon-cloud-download"})])]),_c('h4',{staticClass:"pubtitle"},[_vm._v("A Long Short-Term Memory (LSTM) Model for Business Sentiment Analysis Based on Recurrent Neural Network")]),_c('div',{staticClass:"pubauthor"},[_vm._v(" Md. Jahidul Islam Razin, "),_c('strong',[_vm._v("Md. Abdul Karim")]),_vm._v(", M. F. Mridha, S. M. Rafiuddin Rifat and Tahira Alam")]),_c('div',{staticClass:"pubcite"},[_c('span',{staticClass:"label label-warning"},[_vm._v("Conference Papers ")]),_vm._v("Sustainable Communication Networks and Application, 1-15 Springer, Singapore.")])]),_c('div',{staticClass:"pubdetails"},[_c('h4',[_vm._v("Abstract")]),_c('p',[_vm._v(" Business sentiment analysis (BSA) is one of the significant and popular topics of natural language processing. It is one kind of sentiment analysis techniques for business purpose. Different categories of sentiment analysis techniques like lexicon-based techniques and different types of machine learning algorithms are applied for sentiment analysis on different languages like English, Hindi, Spanish, etc. In this paper, long short-term memory (LSTM) is applied for business sentiment analysis, where recurrent neural network is used. LSTM model is used in a modified approach to prevent the vanishing gradient problem rather than applying the conventional recurrent neural network (RNN). To apply the modified RNN model, product review dataset is used. In this experiment, 70% of the data is trained for the LSTM and the rest 30% of the data is used for testing. The result of this modified RNN model is compared with other conventional RNN models and a comparison is made among the results. It is noted that the proposed model performs better than the other conventional RNN models. Here, the proposed model, i.e., modified RNN model approach has achieved around 91.33% of accuracy. By applying this model, any business company or e-commerce business site can identify the feedback from their customers about different types of product that customers like or dislike. Based on the customer reviews, a business company or e-commerce platform can evaluate its marketing strategy. ")])])]),_c('div',{staticClass:"item mix cpaper",attrs:{"data-year":"2021"}},[_c('div',{staticClass:"pubmain"},[_c('div',{staticClass:"pubassets"},[_c('a',{staticClass:"pubcollapse",attrs:{"href":"#"}},[_c('i',{staticClass:"icon-eye-open"})]),_c('a',{staticClass:"tooltips",attrs:{"href":"https://doi.org/10.1007/978-3-030-86970-0_24","title":"External link","target":"_blank"}},[_c('i',{staticClass:"icon-external-link"})]),_c('a',{staticClass:"tooltips",attrs:{"href":"https://doi.org/10.1007/978-3-030-86970-0_24","title":"Download","target":"_blank"}},[_c('i',{staticClass:"icon-cloud-download"})])]),_c('h4',{staticClass:"pubtitle"},[_vm._v("Semantic Improvement of Question-Answering System with Multi-Layer LSTM")]),_c('div',{staticClass:"pubauthor"},[_vm._v(" Hanif Bhuiyan, "),_c('strong',[_vm._v("Md. Abdul Karim")]),_vm._v(", Faria Benta Karim and Jinat Ara")]),_c('div',{staticClass:"pubcite"},[_c('span',{staticClass:"label label-warning"},[_vm._v("Conference Papers ")]),_vm._v("International Conference on Computational Science and Its Applications, 338-352 Springer, Cham.")])]),_c('div',{staticClass:"pubdetails"},[_c('h4',[_vm._v("Abstract")]),_c('p',[_vm._v(" Business sentiment analysis (BSA) is one of the significant and popular topics of natural language processing. It is one kind of sentiment analysis techniques for business purpose. Different categories of sentiment analysis techniques like lexicon-based techniques and different types of machine learning algorithms are applied for sentiment analysis on different languages like English, Hindi, Spanish, etc. In this paper, long short-term memory (LSTM) is applied for business sentiment analysis, where recurrent neural network is used. LSTM model is used in a modified approach to prevent the vanishing gradient problem rather than applying the conventional recurrent neural network (RNN). To apply the modified RNN model, product review dataset is used. In this experiment, 70% of the data is trained for the LSTM and the rest 30% of the data is used for testing. The result of this modified RNN model is compared with other conventional RNN models and a comparison is made among the results. It is noted that the proposed model performs better than the other conventional RNN models. Here, the proposed model, i.e., modified RNN model approach has achieved around 91.33% of accuracy. By applying this model, any business company or e-commerce business site can identify the feedback from their customers about different types of product that customers like or dislike. Based on the customer reviews, a business company or e-commerce platform can evaluate its marketing strategy. ")])])])])])])])])])}]

